/*eslint-disable*/
import React from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
import { NavLink, useLocation } from "react-router-dom";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Icon from "@material-ui/core/Icon";
// core components
import AdminNavbarLinks from "components/Navbars/AdminNavbarLinks.js";
import RTLNavbarLinks from "components/Navbars/RTLNavbarLinks.js";

import styles from "assets/jss/material-dashboard-react/components/sidebarStyle.js";

import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from "@material-ui/core";
import moment from "moment";

const useStyles = makeStyles(styles);

export default function Sidebar(props) {
	const classes = useStyles();
	let location = useLocation();
	// verifies if routeName is the one active (in browser input)
	function activeRoute(routeName) {
		return location.pathname === routeName;
	}
	const { color, logo, image, logoText, routes } = props;
	var links = (
		<List className={classes.list}>
			{routes.map((prop, key) => {
				var activePro = " ";
				var listItemClasses;
				if (prop.path === "/upgrade-to-pro") {
					activePro = classes.activePro + " ";
					listItemClasses = classNames({
						[" " + classes[color]]: true,
					});
				} else {
					listItemClasses = classNames({
						[" " + classes[color]]: activeRoute(prop.layout + prop.path),
					});
				}
				const whiteFontClasses = classNames({
					[" " + classes.whiteFont]: activeRoute(prop.layout + prop.path),
				});
				return (
					<NavLink
						to={prop.layout + prop.path}
						className={activePro + classes.item}
						activeClassName='active'
						key={key}
						// onClick={() => (window.location = prop.layout + prop.path)}
					>
						<ListItem button className={classes.itemLink + listItemClasses}>
							{typeof prop.icon === "string" ? (
								<Icon
									className={classNames(classes.itemIcon, whiteFontClasses, {
										[classes.itemIconRTL]: props.rtlActive,
									})}
								>
									{prop.icon}
								</Icon>
							) : (
								<prop.icon
									className={classNames(classes.itemIcon, whiteFontClasses, {
										[classes.itemIconRTL]: props.rtlActive,
									})}
								/>
							)}
							<ListItemText
								primary={props.rtlActive ? prop.rtlName : prop.name}
								className={classNames(classes.itemText, whiteFontClasses, {
									[classes.itemTextRTL]: props.rtlActive,
								})}
								disableTypography={true}
							/>
						</ListItem>
					</NavLink>
				);
			})}
			{/* <NavLink
				activeClassName='active'
				key={1}
				// onClick={() => (window.location = prop.layout + prop.path)}
			>
			</NavLink> */}
		</List>
	);
	var brand = (
		<div className={classes.logo}>
			<a
				href='https://www.creative-tim.com?ref=mdr-sidebar'
				className={classNames(classes.logoLink, {
					[classes.logoLinkRTL]: props.rtlActive,
				})}
				target='_blank'
			>
				<div className={classes.logoImage}>
					<img src={logo} alt='logo' className={classes.img} />
				</div>
				{logoText}
			</a>
		</div>
	);

	const Tabla = () => {
		return (
			<div style={{ fontSize: "0.8rem" }}>
				<h5>Eventos</h5>
				<TableContainer component={Paper} style={{ padding: "0px", marginTop: "10px" }}>
					<Table sx={{}} size='small' aria-label='a dense table'>
						<TableHead>
							<TableRow>
								<TableCell>ETA</TableCell>
								<TableCell align='right'>Evento</TableCell>
							</TableRow>
						</TableHead>
						<TableBody>
							{[
								{ descripcion: "Evento 1", eta: moment().format("DD/MM/YYYY HH:mm") },
								{ descripcion: "Evento 2", eta: moment().format("DD/MM/YYYY HH:mm") },
								{ descripcion: "Evento 3", eta: moment().format("DD/MM/YYYY HH:mm") },
								{ descripcion: "Evento 4", eta: moment().format("DD/MM/YYYY HH:mm") },
							].map((row, idx) => (
								<TableRow key={idx} sx={{ "&:last-child td, &:last-child th": { border: 0 } }}>
									<TableCell component='th' scope='row'>
										{row.eta}
									</TableCell>
									<TableCell align='right'>{row.descripcion}</TableCell>
								</TableRow>
							))}
						</TableBody>
					</Table>
				</TableContainer>
			</div>
		);
	};

	return (
		<div>
			<Hidden mdUp implementation='css'>
				<Drawer
					variant='temporary'
					anchor={props.rtlActive ? "left" : "right"}
					open={props.open}
					classes={{
						paper: classNames(classes.drawerPaper, {
							[classes.drawerPaperRTL]: props.rtlActive,
						}),
					}}
					onClose={props.handleDrawerToggle}
					ModalProps={{
						keepMounted: true, // Better open performance on mobile.
					}}
				>
					{brand}
					<div className={classes.sidebarWrapper}>
						{props.rtlActive ? <RTLNavbarLinks /> : <AdminNavbarLinks />}
						{links}
					</div>
					{image !== undefined ? (
						<div className={classes.background} style={{ backgroundImage: "url(" + image + ")" }} />
					) : null}
				</Drawer>
			</Hidden>
			<Hidden smDown implementation='css'>
				<Drawer
					anchor={props.rtlActive ? "right" : "left"}
					variant='permanent'
					open
					classes={{
						paper: classNames(classes.drawerPaper, {
							[classes.drawerPaperRTL]: props.rtlActive,
						}),
					}}
				>
					{brand}
					<div className={classes.sidebarWrapper}>
						{links}
						{/* <Tabla /> */}
					</div>
					{image !== undefined ? (
						<div className={classes.background} style={{ backgroundImage: "url(" + image + ")" }} />
					) : null}
				</Drawer>
			</Hidden>
		</div>
	);
}

Sidebar.propTypes = {
	rtlActive: PropTypes.bool,
	handleDrawerToggle: PropTypes.func,
	//   bgColor: PropTypes.oneOf(["purple", "blue", "green", "orange", "red"]),
	bgColor: PropTypes.oneOf(["blue"]),
	logo: PropTypes.string,
	image: PropTypes.string,
	logoText: PropTypes.string,
	routes: PropTypes.arrayOf(PropTypes.object),
	open: PropTypes.bool,
};
