import React, { useState, useEffect } from "react";
import { Line } from "react-chartjs-2";
import { InsertChartOutlined } from "@material-ui/icons";
import {
	List,
	Menu,
	ListItem,
	ListItemText,
	MenuItem,
	Table,
	TableContainer,
	TableHead,
	TableRow,
	TableCell,
	TableBody,
	Paper,
	CircularProgress,
	Box,
	Chip,
	Button,
} from "@material-ui/core";
import moment from "moment";
import _ from "lodash";
import { useLocation } from "react-router-dom";
import GridItem from "components/Grid/GridItem";
import GridContainer from "components/Grid/GridContainer";

const _data = {
	labels: ["Red", "Orange", "Blue"],
	datasets: [
		{
			label: "",
			data: [0],
			backgroundColor: ["#aaa"],
			borderWidth: 1.5,
		},
	],
};

const useQuery = () => new URLSearchParams(useLocation().search);

const Intervalos = [1, 5, 10, 15, 20, 25, 30]; // Minutos.
const Periodos = [1, 6, 12, 18, 24, 48, 72, 96, 120, 144, 168, 192, 216]; // Horas -> Hasta 9 días.

export default function Historial() {
	/**
	 * Para capturar el {id} que puede o no venir como query parameter.
	 */
	const query = useQuery();

	/**
	 * Para el manejo de las referencias de los filtros.
	 */
	const [anchorEl, setAnchorEl] = React.useState(null);
	const [anchorElPeriodos, setAnchorElPeriodos] = React.useState(null);
	const [anchorElIntervalos, setAnchorElIntervalos] = React.useState(null);

	/**
	 * Filtros.
	 */
	const [periodo, setPeriodo] = useState(Periodos[Periodos.length - 1]); // Defecto: 9 días.
	const [intervalo, setIntervalo] = useState(30); // Defecto: 30 min.

	/**
	 * Para el grafico.
	 */
	const [values, setValues] = useState([]);
	const [chartData, setChartData] = useState(_data);
	const [address, setAddress] = React.useState(undefined);
	const [datosAMostrar, setDatosAMostrar] = useState([]);
	const [ultimaLectura, setUltimaLectura] = useState(undefined);
	const [partidas, setPartidas] = useState([]);
	const [partida, setPartida] = useState(undefined);

	/**
	 * Para los datos adicionales que se muestran debajo del grafico.
	 */
	const [datosAdicionales, setDatosAdicionales] = React.useState(undefined);

	/**
	 * Para mostrar los resúmenes debajo del gráfico.
	 */
	const [valorMinimo, setValorMinimo] = useState(999999);
	const [valorMaximo, setValorMaximo] = useState(0);

	/**
	 * Traemos los datos que se encuentran definidos como graficables.
	 */
	useEffect(() => {
		let dispached = false;

		(async () => {
			if (dispached) return;

			/**
			 * Las direcciones se encuentran definidas en el mismo servidor. Evitando problemas de sincronización.
			 */
			const _datos = await fetch(`${process.env.REACT_APP_SERVER_URL}/Api/PLC/datos/realTime/direcciones`);
			const { resultados: _datosAMostrar } = await _datos.json();

			const _datosPartidas = await fetch(
				`${process.env.REACT_APP_SERVER_URL}/Api/PLC/datos/realTime/resumen/historial/partidas`
			);
			const { resultados: _PartidasAMostrar } = await _datosPartidas.json();

			if (_PartidasAMostrar.length > 0) {
				console.log(_PartidasAMostrar);
				setPartidas(_PartidasAMostrar);
			}

			/**
			 * En caso de tener {id} definido en la URL, determinamos que sea un {id} válido definido como graficable.
			 * Siendo afirmativo, lo definimos como valor por defecto en los filtros de tipo.
			 */
			if (_datosAMostrar.length > 0) {
				const _addr = _datosAMostrar.findIndex((a) => a.id == query.get("id"));
				let idx = 0;
				if (_addr > -1) idx = _addr;

				setAddress(_datosAMostrar[idx]);
			}

			setDatosAMostrar(_datosAMostrar);

			if (query.get("partida")) {
				setPartida(query.get("partida"));
			}
		})();

		return () => {
			dispached = true;
		};
	}, []);

	/**
	 * Eventos para los filtros.
	 * XXX: Inicio
	 */
	const open = Boolean(anchorEl);
	const openPeriodos = Boolean(anchorElPeriodos);
	const openIntervalos = Boolean(anchorElIntervalos);

	const handleClickListItem = (event) => {
		setAnchorEl(event.currentTarget);
	};

	const handleClickListItemPeriodos = (event) => {
		setAnchorElPeriodos(event.currentTarget);
	};

	const handleClickListItemIntervalos = (event) => {
		setAnchorElIntervalos(event.currentTarget);
	};

	const handleMenuItemClick = (event, index) => {
		setAddress(index);
		setAnchorEl(null);
	};

	const handleMenuItemClickPeriodos = (event, index) => {
		setPeriodo(index);
		setAnchorElPeriodos(null);
	};

	const handleMenuItemClickIntervalos = (event, index) => {
		setIntervalo(index);
		setAnchorElIntervalos(null);
	};

	const handleClose = () => {
		setAnchorEl(null);
	};

	const handleClosePeriodos = () => {
		setAnchorElPeriodos(null);
	};

	const handleCloseIntervalos = () => {
		setAnchorElIntervalos(null);
	};

	/**
	 * Eventos para los filtros.
	 * XXX: Fin
	 */
	useEffect(() => {
		let dispached = false;

		(async () => {
			if (dispached || !address) return;

			console.log("Entro");

			const _partida = query.get("partida") || "0";

			const resp = await fetch(
				`${process.env.REACT_APP_SERVER_URL}/Api/PLC/datos/graficables/por_partida/${address.id}/${_partida}`
			);
			const { resultados: respJson } = await resp.json();

			setValues(respJson);
		})();

		return () => {
			dispached = true;
		};
	}, [address, periodo, intervalo, ultimaLectura]);

	/**
	 * Al traer del servidor los valores, los asignamos al gráfico.
	 */
	useEffect(() => {
		if (!address) {
			setChartData(_data);
			return;
		}
		fetchPrices();
	}, [values]);

	const fetchPrices = async () => {
		/**
		 * Limpio las variables para cuando cambian la periocidad e intervalo sobre el mismo conjunto de datos.
		 */
		setValorMaximo(0);
		setValorMinimo(9999999);

		/**
		 * Filtro los datos traidos del servidor.
		 * XXX: Se hacen cortes cada {intervalo} en minutos. Y se trae siempre el primer dato posterior al último dato filtrado.
		 * Se hace de esta manera para, mas adelante, saltearse los eventos de apagado y que el gráfico sea continuo.
		 */
		const _data = _procesarDatosAGraficar();

		let _label = "";

		if (_data.length > 0) {
			const _direccion = datosAMostrar.find((d) => d.id == _data[0].Address);

			if (_direccion) {
				_label = `${_direccion.descripcion.trim()} ( ${_direccion.unidad} )`;
			}
		}

		setChartData({
			labels: _data.map(_darFormatoFecha),
			datasets: [
				{
					label: _label,
					data: _data.map(_valorComoNumero),
					backgroundColor: ["#aaa"],
					borderWidth: 1.5,
				},
			],
		});
	};

	const _valorComoNumero = (v) => {
		let _val = parseInt(v.Value).toString();

		const _direccion = datosAMostrar.find((d) => d.id == v.Address);

		if (_direccion && _direccion.precision > 0)
			_val = (_val / Math.pow(10, _direccion.precision)).toFixed(_direccion.precision);

		return _val;
	};

	const _darFormatoFecha = (v) => moment.unix(v.StartTime).format("DD/MM/YYYY HH:mm:ss");

	const _procesarDatosAGraficar = () => {
		let _ultimo = 0;
		const _currId = address.id;

		const data = _.filter(values, (v) => {
			if (!v.Encendido) return false;

			if (_ultimo === 0) _ultimo = v.StartTime;

			// XXX: Acá se va a agregar, posteriormente, un filtro adicional para evitar los eventos de apagado. Falta definir el algoritmo.
			// Se trabaja con valores unix para tener precision de segundos.
			if (v.StartTime >= _ultimo) {
				_ultimo = moment.unix(v.StartTime).add(intervalo, "minutes").unix();
				return true;
			}

			return false;
		});

		/**
		 * En caso de que no entre la ultima lectura en el rango solicitado,
		 * unicamente en caso de que se trate de una lectura en período de apagado.
		 */
		if (values.length > 0) {
			const _v = values[values.length - 1];
			if (_v.Encendido) data.push(_v);
			/**
			 * Agregamos la primer lectura, tambien unicamente en caso de que el equipo no se encuentre apagado.
			 */
			const _vPrimera = values[0];
			// console.log(_vPrimera);
			if (_vPrimera.Encendido) data.slice().unshift(_vPrimera);

			if (data.length < 2) {
				const _p = datosAdicionales.Periodos;
				setUltimaLectura(moment.unix(_p[_p.length - 2].fin));
			}
		}

		return data;
	};

	useEffect(() => {
		(async () => {
			if (datosAdicionales || !address) return;
			/**
			 * Busco los datos adicionales.
			 */
			const resp = await fetch(
				`${process.env.REACT_APP_SERVER_URL}/Api/PLC/datos/realTime/resumen/historial/por_partida/${partida}`
			);
			const { resultados: respJson } = await resp.json();

			setDatosAdicionales(respJson);
		})();
	}, [chartData]);

	const DireccionDropdownMenu = () => {
		return (
			<>
				<List component='nav' aria-label='Mostrando datos sobre' sx={{ bgcolor: "background.paper" }}>
					<ListItem
						button
						id='lock-button'
						aria-haspopup='listbox'
						aria-controls='lock-menu'
						aria-label='Mostrando datos sobre'
						aria-expanded={open ? "true" : undefined}
						onClick={handleClickListItem}
					>
						<ListItemText primary='Mostrando datos sobre' secondary={address.descripcion} />
					</ListItem>
				</List>
				<Menu
					id='lock-menu'
					anchorEl={anchorEl}
					open={open}
					onClose={handleClose}
					MenuListProps={{
						"aria-labelledby": "lock-button",
						role: "listbox",
					}}
				>
					{datosAMostrar.map((option, index) => (
						<MenuItem
							key={index}
							disabled={option.id === address.id}
							selected={option.id === address.id}
							onClick={(event) => handleMenuItemClick(event, option)}
						>
							{option.descripcion}
						</MenuItem>
					))}
				</Menu>
			</>
		);
	};

	const PeriodosDropdownMenu = () => {
		return (
			<>
				<List component='nav' aria-label='Periodo de Tiempo' sx={{ bgcolor: "background.paper" }}>
					<ListItem
						button
						id='lock-button'
						aria-haspopup='listbox'
						aria-controls='lock-menu'
						aria-label='Periodo de Tiempo'
						aria-expanded={openPeriodos ? "true" : undefined}
						onClick={handleClickListItemPeriodos}
					>
						<ListItemText
							primary='Periodo de Tiempo'
							secondary={
								periodo % 24 == 0 && periodo > 24
									? `Últimos ${periodo / 24} días`
									: `Últimas ${periodo} Hs`
							}
						/>
					</ListItem>
				</List>
				<Menu
					id='lock-menu'
					anchorEl={anchorElPeriodos}
					open={openPeriodos}
					onClose={handleClosePeriodos}
					MenuListProps={{
						"aria-labelledby": "lock-button",
						role: "listbox",
					}}
				>
					{Periodos.map((_periodo, index) => (
						<MenuItem
							key={index}
							disabled={_periodo === periodo}
							selected={_periodo === periodo}
							onClick={(event) => handleMenuItemClickPeriodos(event, _periodo)}
						>
							{_periodo % 24 == 0 && _periodo > 24
								? `Últimos ${_periodo / 24} días`
								: `Últimas ${_periodo} Hs`}
						</MenuItem>
					))}
				</Menu>
			</>
		);
	};

	const IntervalosDropdownMenu = () => {
		return (
			<>
				<List component='nav' aria-label='Intervalo de Tiempo' sx={{ bgcolor: "background.paper" }}>
					<ListItem
						button
						id='lock-button'
						aria-haspopup='listbox'
						aria-controls='lock-menu'
						aria-label='Intervalo de Tiempo'
						aria-expanded={openIntervalos ? "true" : undefined}
						onClick={handleClickListItemIntervalos}
					>
						<ListItemText primary='Intervalo de Tiempo' secondary={`${intervalo} Min`} />
					</ListItem>
				</List>
				<Menu
					id='lock-menu'
					anchorEl={anchorElIntervalos}
					open={openIntervalos}
					onClose={handleCloseIntervalos}
					MenuListProps={{
						"aria-labelledby": "lock-button",
						role: "listbox",
					}}
				>
					{Intervalos.map((_intervalo, index) => (
						<MenuItem
							key={index}
							disabled={_intervalo === intervalo}
							selected={_intervalo === intervalo}
							onClick={(event) => handleMenuItemClickIntervalos(event, _intervalo)}
						>
							{`${_intervalo} Min`}
						</MenuItem>
					))}
				</Menu>
			</>
		);
	};

	return address ? (
		<div>
			<GridContainer direction='row'>
				<GridItem xs={12} md={4} sm={4}>
					{PeriodosDropdownMenu()}
				</GridItem>
				<GridItem xs={12} md={4} sm={4}>
					{IntervalosDropdownMenu()}
				</GridItem>
				<GridItem xs={12} md={4} sm={4}>
					{DireccionDropdownMenu()}
				</GridItem>

				{partida && (
					<>
						<Button
							size='small'
							variant='outlined'
							color='primary'
							startIcon={<InsertChartOutlined />}
							style={{
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							onClick={() => (window.location = `/admin/historial/partidas`)}
						>
							{`Volver al listado de Partidas`}
						</Button>
						<GridItem xs={12} md={12} sm={12}>
							<Line
								data={chartData}
								options={{
									borderColor: "#353535",
									responsive: true,
									animation: { easing: "linear" },
									elements: { line: { tension: 0.1 } },
								}}
							/>
						</GridItem>
						{datosAdicionales && partida && (
							<GridItem xs={12} md={12} sm={12}>
								<GridContainer>
									<GridItem xs={12} md={12} sm={12}>
										<h3>Datos adicionales</h3>
									</GridItem>
									<GridItem xs={12} md={6} sm={6}>
										<TableContainer component={Paper}>
											<Table sx={{ minWidth: 650 }} aria-label='simple table'>
												<TableBody>
													{datosAdicionales.total && (
														<TableRow
															key={"total"}
															sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
														>
															<TableCell component='th' scope='row'>
																Tiempo total de Proceso:
															</TableCell>
															<TableCell align='right'>
																~{datosAdicionales.total.asString}
															</TableCell>
														</TableRow>
													)}

													{datosAdicionales.encendido && (
														<TableRow
															key={"encendido"}
															sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
														>
															<TableCell component='th' scope='row'>
																Tiempo neto de trabajo:
															</TableCell>
															<TableCell align='right'>
																~{datosAdicionales.encendido.asString}
															</TableCell>
														</TableRow>
													)}

													{datosAdicionales.apagado && (
														<TableRow
															key={"apagado"}
															sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
														>
															<TableCell component='th' scope='row'>
																Tiempo total detenido:
															</TableCell>
															<TableCell align='right'>
																~{datosAdicionales.apagado.asString}
															</TableCell>
														</TableRow>
													)}
												</TableBody>
											</Table>
										</TableContainer>
									</GridItem>
									<GridItem xs={12} md={6} sm={6}>
										<TableContainer component={Paper}>
											<Table sx={{ minWidth: 650 }} aria-label='simple table' size='small'>
												<TableHead>
													<TableRow>
														<TableCell colSpan={2}>Eventos</TableCell>
													</TableRow>
												</TableHead>
												<TableBody>
													{datosAdicionales.Eventos &&
														datosAdicionales.Eventos.length > 0 &&
														datosAdicionales.Eventos.map((row, i) => (
															<TableRow
																key={i}
																sx={{
																	"&:last-child td, &:last-child th": { border: 0 },
																}}
															>
																<TableCell component='th' scope='row'>
																	<Chip
																		className={
																			row.Value
																				? "success-color"
																				: "warning-color"
																		}
																		size='small'
																		label={row.Value ? "Se encendió" : "Se detuvo"}
																		style={{
																			color: "whitesmoke",
																			minWidth: "77px",
																			fontSize: "small",
																			fontVariantCaps: "small-caps",
																			marginRight: "15px",
																			borderRadius: "5px",
																		}}
																	/>
																	{row.Descripcion}
																</TableCell>
																<TableCell align='right'>{row.asString}</TableCell>
															</TableRow>
														))}
												</TableBody>
											</Table>
										</TableContainer>
									</GridItem>
								</GridContainer>
							</GridItem>
						)}
					</>
				)}
			</GridContainer>
		</div>
	) : (
		<Box sx={{ display: "flex" }}>
			<CircularProgress />
		</Box>
	);
}
