import React, { useState, useEffect } from "react";
// @material-ui/core
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
import { Timer, Settings, InsertChart, InsertChartOutlined } from "@material-ui/icons";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardIcon from "components/Card/CardIcon.js";
import CardFooter from "components/Card/CardFooter.js";
import CardBody from "components/Card/CardBody";
import { Chip, Grid } from "@material-ui/core";
import { Button, Backdrop, CircularProgress } from "@material-ui/core";

import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";

import { useLocation } from "react-router-dom";

const useQuery = () => new URLSearchParams(useLocation().search);

const useStyles = makeStyles(styles);

const Dashboard = () => {
	let query = useQuery();

	const [segundos, setSegundos] = useState(0);
	const [datos, setDatos] = useState([]);
	const [datosEventosFijos, setDatosEventosFijos] = useState([]);
	const [datosProducto, setDatosProducto] = useState({});

	useEffect(() => {
		let disposed = false;

		(async () => {
			if (disposed) return;
			console.log(process.env.REACT_APP_SERVER_URL);
			await traerDatos();
		})();

		return () => {
			disposed = true;
		};
	}, []);

	useEffect(() => {
		let disposed = false;

		const interval = setInterval(() => {
			if (disposed) return;

			if (segundos === 1) {
				(async () => {
					await traerDatos();
				})();
			}

			const _seg = segundos - 1;
			setSegundos(_seg > 0 ? _seg : 15);
		}, 1000);

		return () => {
			clearInterval(interval);
			disposed = true;
		};
	}, [segundos]);

	const traerDatos = async () => {
		try {
			const _data = await fetch(`${process.env.REACT_APP_SERVER_URL}/Api/PLC/datos/realTime`);
			const { resultados: _dataJson } = await _data.json();

			setDatos(_dataJson);

			const _dataEventosFijos = await fetch(
				`${process.env.REACT_APP_SERVER_URL}/Api/PLC/datos/realTime/eventos_fijos`
			);
			const { resultados: _dataEventosFijosJson } = await _dataEventosFijos.json();

			setDatosEventosFijos(_dataEventosFijosJson);

			const _dataProducto = await fetch(`${process.env.REACT_APP_SERVER_URL}/Api/PLC/datos/realTime/producto`);
			const { resultados: _dataProductoJson } = await _dataProducto.json();

			setDatosProducto(_dataProductoJson);
		} catch (error) {
			console.log(error);
		}
	};

	const classes = useStyles();

	const TarjetaValoresTiempoReal = ({ id, value, descripcion, unidad, seteo }) => {
		return (
			<GridItem xs={12} sm={6} md={4} key={id}>
				<Card>
					<CardHeader color='success' stats icon>
						<CardIcon color='success'>
							<span className='Valor_Actual'>
								{value} {unidad}
							</span>
						</CardIcon>
						{seteo > 0 && (
							<>
								<p className={classes.cardCategory}>Seteo</p>
								<h3 className={classes.cardTitle}>
									{parseFloat(seteo).toFixed(2)} <small>{unidad}</small>
								</h3>
							</>
						)}
					</CardHeader>
					<CardFooter stats>
						<Button
							size='small'
							variant='outlined'
							color='primary'
							startIcon={<InsertChartOutlined />}
							style={{ display: "flex", alignItems: "center", justifyContent: "center" }}
							onClick={() =>
								(window.location = `/admin/monitoreo?id=${id}&partida=${datosProducto.Partida ?? 0}`)
							}
						>
							Monitorear
						</Button>
						<div className={classes.stats}>{descripcion}</div>
					</CardFooter>
				</Card>
			</GridItem>
		);
	};

	const FichaProducto = () => {
		const { Partida, CodProducto, DescProducto, KilosProducto } = datosProducto;
		return (
			<GridItem xs={12} sm={12} md={6}>
				<Card>
					<CardHeader color='primary' stats icon>
						<GridContainer>
							<GridItem xs={12} sm={12} md={6} className='flex-centrar'>
								<CardIcon color='primary'>
									<span className='Valor_Actual'>Partida: {Partida || ""}</span>
								</CardIcon>
							</GridItem>
							<GridItem xs={12} sm={12} md={6}>
								<GridContainer>
									<GridItem xs={12} sm={12} md={12}>
										<p className={`${classes.cardCategory} flex-centrar-vertical`}>
											{KilosProducto} Kgs.
										</p>
									</GridItem>
									<GridItem xs={12} sm={12} md={12}>
										<h3 className={`${classes.cardTitle} flex-centrar-vertical`}>
											<small>{CodProducto}</small>
										</h3>
									</GridItem>
								</GridContainer>
							</GridItem>
						</GridContainer>
					</CardHeader>
					<CardFooter stats style={{ marginTop: 27 }}>
						<div className={classes.stats}>
							<Timer />
							<span>{segundos}</span>
						</div>
						<p className={classes.cardCategory}>{DescProducto}</p>
					</CardFooter>
				</Card>
			</GridItem>
		);
	};

	return (
		<div>
			{/* <Backdrop sx={{ color: "#fff", zIndex: (theme) => 9999 }} open={true} onClick={() => {}}>
				<CircularProgress color='inherit' />
			</Backdrop> */}
			<GridContainer>
				<FichaProducto />
				<GridItem xs={12} sm={12} md={6}>
					<Card>
						<CardBody>
							<Grid container spacing={1}>
								{datosEventosFijos.map(({ id, descripcion, value }) => {
									let _style = "default-color";

									if (parseInt(value) === 1) _style = "success-color";
									if (parseInt(value) === 2) _style = "warning-color";

									return (
										<Grid
											item
											md={3}
											xs={6}
											key={id}
											style={{ display: "flex", justifyContent: "center", alignItems: "center" }}
										>
											<Chip
												className={_style}
												color='default'
												label={descripcion}
												style={{
													// backgroundColor: _color,
													color: "whitesmoke",
													minWidth: "106px",
												}}
											/>
										</Grid>
									);
								})}
							</Grid>
						</CardBody>
						<CardFooter stats>
							<Grid container spacing={1} className='flex-centrar-horizontal'>
								<Grid
									item
									md={4}
									sm={12}
									style={{ display: "flex", justifyContent: "center", alignItems: "center" }}
								>
									<Chip
										size='small'
										className={"default-color"}
										color='default'
										label='Apagado'
										style={{
											color: "whitesmoke",
											minWidth: "106px",
										}}
									/>
								</Grid>
								<Grid
									item
									md={4}
									sm={12}
									style={{ display: "flex", justifyContent: "center", alignItems: "center" }}
								>
									<Chip
										size='small'
										className={"success-color"}
										color='default'
										label='Encendido'
										style={{
											color: "whitesmoke",
											minWidth: "106px",
										}}
									/>
								</Grid>
								<Grid
									item
									md={4}
									sm={12}
									style={{ display: "flex", justifyContent: "center", alignItems: "center" }}
								>
									<Chip
										size='small'
										className={"warning-color"}
										color='default'
										label='Error'
										style={{
											color: "whitesmoke",
											minWidth: "106px",
										}}
									/>
								</Grid>
							</Grid>
						</CardFooter>
					</Card>
				</GridItem>
			</GridContainer>
			<GridContainer>{datos.map(TarjetaValoresTiempoReal)}</GridContainer>
		</div>
	);
};

export default Dashboard;
